/*---------------*/
/***** Duke *****/
/*---------------*/

figure.effect-duke
{
	background  : rgba(255,255,255,0.9);
	overflow    :hidden;
}

figure.effect-duke img,
figure.effect-duke:hover .icon-holder {
	opacity 			: 1;
	top 				: 40%;
	-webkit-transition  : all 0.25s, -webkit-transform 0.25s;
	transition 			: all 0.25s, transform 0.25s;
	z-index 			: 999;
}

figure.effect-duke:hover img {
	opacity 			: 1;
	-webkit-transform 	: scale3d(1.2,1.2,1);
	transform 			: scale3d(1.2,1.2,1);
}
figure.effect-duke:hover p {
	opacity 			: 1 !important;
	-webkit-transform 	: scale3d(1.2,1.2,1);
	transform 			: scale3d(1.2,1.2,1);
    color:#515151;
}

figure.effect-duke h2 {
	-webkit-transition 		: -webkit-transform 0.35s;
	transition 				: transform 0.35s;
	-webkit-transform 		: scale3d(0.8,0.8,1);
	transform 				: scale3d(0.8,0.8,1);
	-webkit-transform-origin: 50% 100%;
	transform-origin 		: 50% 100%;
    color:#fff;
}
figure.effect-duke h3 {
	-webkit-transition 		: -webkit-transform 0.35s;
	transition 				: transform 0.35s;
	-webkit-transform 		: scale3d(0.8,0.8,1);
	transform 				: scale3d(0.8,0.8,1);
	-webkit-transform-origin: 50% 100%;
	transform-origin 		: 50% 100%;
    color:#000;
}

figure.effect-duke .icon-holder {
	position 				: absolute;
	top 					: 30%;
	left 					: 0;
	margin 					: 20px;
	padding 				: 30px;
	text-transform 			: none;
	font-size 				: 90%;
	opacity 				: 0;
	width 					: 100%;
	margin 					: auto;
	-webkit-transform 		: scale3d(0.8,0.8,1);
	transform 				: scale3d(0.8,0.8,1);
	-webkit-transform-origin: 50% -100%;
	transform-origin 		: 50% -100%;
    text-align:center
}

figure.effect-duke .icon-holder a {margin:0 30px !important}
figure.effect-duke .icon-holder i {font-size:32px}

figure.effect-duke .icon-holder ul{width:100%;margin:auto;}


figure.effect-duke:hover h2,
figure.effect-duke:hover h3,
figure.effect-duke:hover p {
	opacity 			: 1;
	-webkit-transform 	: scale3d(1,1,1);
	transform 			: scale3d(1,1,1);
}

