                  .nav-link i.fad,
                  .nav-link i.fab {
                    top: unset !important; }
                  
                  .btn.btn-twitter.btn-icon-only .btn-inner--icon i,
                  .btn.btn-facebook.btn-icon-only .btn-inner--icon i,
                  .btn.btn-dribble.btn-icon-only .btn-inner--icon i,
                  .btn.btn-gihtub.btn-icon-only .btn-inner--icon i {
                    top: -17px; }
                  
                  .choices[data-type*="select-multiple"] .choices__button:after,
                  .choices[data-type*="text"] .choices__button:after {
                    font-weight: 100;
                    /* font-family: "Font Awesome 5 Free"; */
                    -webkit-font-smoothing: antialiased;
                    display: inline-block;
                    font-style: normal;
                    font-feature-settings: normal;
                    font-variant: normal;
                    text-rendering: auto;
                    line-height: 1.5;
                    content: "\f00d";
                    right: 19px;
                    top: 0px;
                    font-size: 11px;
                    text-indent: 1px;
                    position: absolute; }



